.fullScreen{
    max-height: 100vh;
    overflow: hidden;
}
.preloader_wrap{
    width: 100vw;
    height: 100vh;
    background-color: #333;
    overflow: hidden;
    position: fixed;
    z-index: 99;
}
.circle-preloader {
    display: block;
    width: 50px;
    height: 50px;
    border: 4px solid #444;
    border-bottom-color: #888;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    animation: spin .75s infinite linear;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}